.review {
	position: relative;
	z-index: 1;
	padding-right: 20px;
	padding-left: 20px;

	&::before {
		content: '';
		position: absolute;
		top: -10px;
		left: 50%;
		width: calc(100% - 20px);
		max-width: 1200px;
		height: 100%;
		background-color: rgba(250, 250, 250, 0.9);
		border-radius: 38px 38px 15% 15%;
		box-shadow: 0 0 50px rgba(0, 0, 0, 0.01);
		transform: translateX(-50%);

		@media (min-width: 1240px) {
			top: -40px;
			width: calc(100% - 40px);
		}
	}

	&::after {
		content: '';
		position: absolute;
		top: -10px;
		left: 50%;
		width: 100vw;
		height: calc(100% + 20px);
		background-image: linear-gradient(to bottom, transparent, #fff, #fff);

		box-shadow: 0 50px 50px #fff;
		transform: translateX(-50%);

		@media (min-width:@min-lg) {
			background-image: linear-gradient(to bottom, transparent 20%, #fff 80%);
		}
	}

	& .swiper-container {
		width: 265px;

		@media (min-width: 640px) {
			width: 550px;
		}

		@media (min-width: 960px) {
			width: 835px;
		}

		@media (min-width: 1200px) {
			width: 100%;
		}
	}
}

.review__wrap {
	z-index: 1;
	padding-right: 0;
	padding-left: 0;
	background-color: #fff;
	border-radius: 28px;
}

@media (min-width:@min-lg) {
	.review__wrap {
		max-width: 1120px;
		padding-right: 30px;
		padding-left: 30px;
	}
}

.review__heading {
	margin-top: 2px;
	margin-bottom: 12px;
	font-size: 18px;
	line-height: 24px;
	color: var(--bright);

	@media (min-width: @min-sm) {
		font-size: 22px;
		line-height: 30px;
	}

	@media (min-width: @min-lg) {
		margin-top: 26px;
		font-size: 40px;
		line-height: 50px;
	}
}

.review__list {
	margin: 0 0 30px;
	padding: 0;
	list-style-type: none;

	@media (min-width: @min-xl) {
		display: block !important;
		// flex-wrap: wrap;
		margin-bottom: 40px;
		columns: 3;
		transform: none !important;
	}
}

.review__sub-heading {
	margin: 0 0 30px;
	font-size: 14px;
	line-height: 20px;
	text-align: center;

	@media (min-width: @min-lg) {
		max-width: 800px;
		margin-right: auto;
		margin-bottom: 54px;
		margin-left: auto;
		font-size: 16px;
		line-height: 22px;
	}
}

.review__item {

	@media (min-width: @min-xl) {
		width: 325px !important;
		margin-right: 0 !important;
		margin-bottom: 40px;
		break-inside: avoid;

		&:not(:nth-child(3n)) {
			margin-right: 42px !important;
		}
	}

	& img {
		border-radius: 20px 0 20px;
	}

	& p {
		margin-bottom: 0;

		font-size: 13px;
		line-height: 20px;
	}
}

.review__user {
	position: relative;
}

.review__name-user {
	position: absolute;
	bottom: 0;
	left: 0;
	padding: 2px 8px;
	color: var(--body);
	background-color: var(--bright);

	@media (min-width:@min-lg) {
		left: -10px;
	}
}

.review__button {
	display: block;
	width: fit-content;
	margin-right: auto;
	margin-left: auto;
}
