.form {
	position: relative;
	z-index: 1;
}

.form__wrap {

	@media (max-width: @max-md) {
		padding-top: 378px;

		&::before {
			content: url(../../../img/form/alina.png);
			position: absolute;
			top: 0;
			left: 50%;
			transform: translateX(-50%);
		}
	}

	@media (min-width: @min-md) {
		&::after {
			content: url(../../../img/form/alina@desktop.png);
			position: absolute;
			right: -290px;
			bottom: 0;
			z-index: -1;
			pointer-events: none;
		}
	}
}

.form__text {
	.hero__text;
}

.form__title {
	.hero__title();
}

.form__title {
	max-width: 836px;

	@media (min-width: @min-md) {
		font-size: 64px;
		line-height: 60px;
	}

	@media (min-width: @min-lg) {
		margin-bottom: 50px;
	}
}

.form__content {
	box-sizing: border-box;
	max-width: 320px;
	margin-right: -10px;
	margin-left: -10px;
	padding: 10px;
	background-color: #fff;
	border-radius: 32px;
	box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);

	@media (min-width: 340px) {
		margin-right: auto;
		margin-left: auto;
	}

	@media (min-width: @min-md) {
		margin-left: -20px;
	}

	@media (min-width: @min-lg) {
		max-width: 440px;
		margin-right: 0;
		margin-left: -20px;
		padding: 0 20px 20px;
		border-radius: 28px;
	}

	& .timer {
		margin-right: auto;
		margin-bottom: 30px;
		margin-left: auto;
		padding: 18px 30px 16px;
		border-radius: 28px;
		box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);

		@media (min-width: @min-lg) {
			position: relative;
			top: -20px;
			margin-bottom: 10px;
			padding: 24px 40px 22px;
		}
	}

	& .timer__title {
		margin-bottom: 10px;
		font-size: 13px;
		line-height: 22px;

		@media (min-width: @min-lg) {
			margin-bottom: 14px;
			font-size: 16px;
			line-height: 22px;
		}
	}

	& .timer__items {
		margin-bottom: 14px;
		font-size: 42px;
		line-height: 18px;
		letter-spacing: -5px;

		@media (min-width: @min-lg) {
			margin-bottom: 20px;
			font-size: 62px;
			letter-spacing: -7px;
		}
	}

	& .timer__item {
		min-width: 57px;

		@media (min-width: @min-lg) {
			min-width: 82px;
		}
	}

	& .timer__end {
		font-size: 13px;
		line-height: 22px;

		@media (min-width: @min-lg) {
			font-size: 16px;
			line-height: 22px;
		}
	}

	& form {
		padding: 0 10px;

		@media (min-width: @min-lg) {
			padding: 0;
		}
	}
}

.form__label {
	position: relative;
	display: block;

	& span {
		position: absolute;
		top: 50%;
		left: 6px;
		z-index: -1;
		font: 10px;
		font-weight: 700;
		font-size: 10px;
		line-height: 20px;
		color: transparent;
		text-transform: uppercase;
		letter-spacing: 1px;
		background-color: transparent;
		transition: color var(--transition-duration) var(--transition-timing-function),
			background-color var(--transition-duration) var(--transition-timing-function);
	}
}

.form__control {
	box-sizing: border-box;
	width: 100%;
	height: 52px;
	margin-bottom: 18px;
	padding: 18px 20px;
	font-size: 16px;
	line-height: 1;
	font-family: inherit;
	color: inherit;
	background-color: #fff;
	border: 2px solid #e5e5e5;
	border-radius: 6px;
	outline: 0;
	transition: border var(--transition-duration) var(--transition-timing-function);
	appearance: none;

	&:focus {
		border-color: var(--text);

		& + span {
			top: -10px;
			z-index: 0;
			padding: 0 5px;
			color: #6e9e4d;
			background-color: #fff;
		}

		&::placeholder {
			color: transparent;
		}
	}

	&:not(:placeholder-shown):not(:focus) {
		border-color: #6e9e4d;

		& + span {
			top: -10px;
			z-index: 0;
			padding: 0 5px;
			color: #6e9e4d;
			background-color: #fff;
		}
	}

	&:invalid {
		color: tomato;
		border-color: tomato;

		& + span {
			color: tomato;
		}
	}
}

.form__button-wrap {
	text-align: center;
}

.form__button {
	@media (min-width: @min-lg) {
		font-size: 16px;
		line-height: 20px;
	}
}

.form__notes {
	font-size: 9px;
	line-height: 20px;
	text-align: center;

	@media (min-width: @min-md) {
		font-size: 10px;
	}

	& img {
		margin-top: 6px;
	}
}
