.result .swiper-container {
	width: 265px;

	@media  (min-width: 640px) {
		width: 550px;
	}

	@media  (min-width: 960px) {
		width: 835px;
	}

	@media  (min-width: 1200px) {
		width: 100%;
	}
}

.result__list {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

.result__item {
	width: 265px;
	padding: 10px;
	box-sizing: border-box;
	background-color: var(--bright-dark);
	border-radius: 30px;
	text-align: center;

	& img {
		display: block;
		max-width: none;
		margin: -10px -10px 0 -10px;
		border-radius: 30px;
	}
}
