.if__list {
	&-big {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-bottom: 20px;

		@media (max-width: 700px) {
			justify-content: center;

			& .if__item {
				max-width: 320px;
			}
		}

		@media (min-width: 700px) {
			& .if__item {
				width: calc(50% - 10px);

				@media (max-width: 900px) {
					&:last-child {
						width: 50%;
						margin-right: auto;
						margin-left: auto;
					}
				}
			}
		}

		@media (min-width: 900px) {
			display: grid;
			grid-template-columns: repeat(3, minmax(250px, 1fr));
			margin-bottom: 30px;
			gap: 30px;

			& .if__item {
				width: 100%;
				margin: 0;
			}
		}
	}

	&-small {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-bottom: 30px;

		@media (min-width: 490px) {
			& .if__item {
				width: calc(33.33% - 10px);

				&:nth-last-child(-n + 2) {
					width: calc(50% - 10px);
					margin-bottom: 0;
				}
			}
		}

		@media (min-width: 800px) {
			& .if__item {
				width: calc(100% / 5 - 25px);

				&:nth-last-child(-n + 2) {
					width: calc(100% / 5 - 25px);
					margin-bottom: 20px;
				}
			}
		}

		@media (min-width: @min-lg) {
			margin-bottom: 60px;
		}

		& .if__item {
			width: calc(50% - 10px);
			margin-bottom: 20px;

			&:last-child {
				width: 100%;
				margin-bottom: 0;
			}
		}
	}
}


.if__item {
	box-sizing: border-box;
	padding: 20px 8px 18px;
	font-size: 11px;
	line-height: 16px;
	text-align: center;
	border-radius: 20px;
	box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);

	&::before {
		content: '';
		display: block;
		width: 70px;
		height: 70px;
		margin: 0 auto 18px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		border-radius: 50%;
	}

	&--big {
		margin: 0 0 20px;
		padding: 20px 28px 16px;
		font-size: 13px;
		line-height: 18px;

		&::before {
			width: 100px;
			height: 100px;
			margin: 0 auto 20px;
		}
	}
}

.if__item {
	&--1::before {
		background-image: url(../../../img/if/slide-1.png);

		@media (min-width: @min-lg) {
			background-image: url(../../../img/if/slide-1@desktop.png);
		}
	}

	&--2::before {
		background-image: url(../../../img/if/slide-2.png);

		@media (min-width: @min-lg) {
			background-image: url(../../../img/if/slide-2@desktop.png);
		}
	}

	&--3::before {
		background-image: url(../../../img/if/slide-3.png);

		@media (min-width: @min-lg) {
			background-image: url(../../../img/if/slide-3@desktop.png);
		}
	}

	&--4::before {
		background-image: url(../../../img/if/slide-4.png);
	}

	&--5::before {
		background-image: url(../../../img/if/slide-5.png);
	}

	&--6::before {
		background-image: url(../../../img/if/slide-6.png);
	}

	&--7::before {
		background-image: url(../../../img/if/slide-7.png);
	}

	&--8::before {
		background-image: url(../../../img/if/slide-8.png);
	}
}

.if__button {
	display: block;
	width: fit-content;
	margin: 0 auto;
}
