.gift {
	margin-top: -40px;
	padding-right: 10px;
	padding-left: 10px;

	@media (min-width: @min-md) {
		margin-top: 0;
		padding-right: 0;
		padding-left: 0;
	}
}

.gift__wrap {
	padding: 20px;
	padding-bottom: 0;
	overflow: hidden;
	background-color: #fff;
	background-clip: padding-box;
	border: 10px solid rgba(245, 245, 245, 0.9);
	border-radius: 28px;
	box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);

	@media (min-width: @min-md) {
		padding-top: 56px;
		border-width: 40px;
		border-radius: 55px;
	}

	@media (min-width: @min-lg) {
		&::after {
			content: url(../../../img/gift/fork.png);
			position: absolute;
			right: -17px;
			bottom: -4px;

			@supports (content: url('../img/gift/fork.webp')) {
				content: url(../../../img/gift/fork.webp);
			}
		}
	}
}

.gift__title {
	margin: 0 0 22px;
	font-weight: 500;
	font-size: 18px;
	line-height: 24px;
	text-align: center;
	color: var(--bright);

	@media (min-width: @min-md) {
		margin-bottom: 52px;
		font-size: 40px;
		line-height: 50px;
	}

	@media (min-width: @min-lg) {
		& span {
			display: block;
		}
	}
}

.gift__article {
	position: relative;
	max-width: 500px;
	margin: 0 auto;
	padding-bottom: 22px;
	text-align: center;
	background-color: #fbf5f4;

	&::after {
		content: url(../../../img/gift/lime.png);
		position: absolute;
		top: -30px;
		right: -93px;

		@supports (content: url('../img/gift/lime.webp')) {
			content: url(../../../img/gift/lime.webp);
		}

		@media (min-width: @min-md) {
			content: url(../../../img/gift/lime@desktop.png);
			top: -60px;
			right: -180px;

			@supports (content: url('../img/gift/lime@desktop.webp')) {
				content: url(../../../img/gift/lime@desktop.webp);
			}
		}
	}

	&::before {
		content: url(../../../img/gift/salmon.png);
		position: absolute;
		bottom: -4px;
		left: -85px;

		@supports (content: url('../img/gift/salmon.webp')) {
			content: url(../../../img/gift/salmon.webp);
		}

		@media (min-width: @min-lg) {
			content: url(../../../img/gift/salmon@desktop.png);
			left: -390px;

			@supports (content: url('../img/gift/salmon@desktop.webp')) {
				content: url(../../../img/gift/salmon@desktop.webp);
			}
		}
	}
}

.gift__article-title {
	margin: 0 0 12px;
	padding: 12px 0;
	font-size: 12px;
	color: #fffbf9;
	background-color: #5b555e;

	@media (min-width: @min-md) {
		padding: 24px 0;
		font-size: 26px;
	}
}

.gift__article p {
	margin: 0;
	margin-bottom: 12px;
	padding-right: 38px;
	padding-left: 38px;
	font-size: 13px;
	line-height: 18px;

	@media (min-width: @min-md) {
		padding-right: 90px;
		padding-left: 90px;
		font-size: 16px;
		line-height: 22px;
	}
}

.gift__button {
	margin-top: 22px;

	@media (min-width: @min-md) {
		margin-top: 54px;
	}
}
