:root {
  --bright: #6e9e4d;
  --bright-light: #99c55f;
  --color-button: #6d9d4d;
  --bright-dark: #58803e;
  --body: #fff;
  --text: #494450;
  --light-text: #e6e6e6;
  --transition-duration: 200ms;
  --transition-timing-function: ease-in-out;
  --mark: #f0a751;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-weight: 500;
  font-family: "Montserrat", sans-serif;
  color: var(--text);
  font-smooth: antialiased;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-size-adjust: 100%;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: none;
}

[class$="__wrap"] {
  position: relative;
  box-sizing: border-box;
  max-width: 1200px;
  margin-right: auto;
  margin-left: auto;
  padding-top: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  padding-left: 20px;
}

main {
  position: relative;
  z-index: 1;
}

.overflow-hidden {
  overflow: hidden;
}

.bright {
  color: var(--bright);
}

[class$="__heading"] {
  position: relative;
  z-index: 1;
  margin: 0 0 20px;
  font-weight: 500;
  font-size: 22px;
  line-height: 30px;
  text-align: center;
}

.wrap-bright-section {
  margin-bottom: -80px;
  padding-top: 50px;
  padding-bottom: 104px;
  background-image: url(../img/bg-green.svg);
  background-position: center;
  background-size: 1920px 100%;
}

.wrap-bright-section section {
  color: var(--body);
  background-color: var(--bright);
}

.mark {
  color: var(--body);
  background-color: var(--mark);
}

.mark-color {
  color: var(--mark);
}

::placeholder {
  color: #a8a8a8;
}

.header {
  display: block;
  box-shadow: 0 50px 50px rgba(0, 0, 0, 0.1);
}

.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: calc(70px - 16px);
  padding-top: 8px;
  padding-bottom: 8px;
}

.logo__link {
  display: block;
}

.logo__link img {
  display: block;
}

.timer {
  width: fit-content;
  background-color: var(--body);
}

.timer p {
  margin: 0;
}

.timer__title {
  display: block;
  font-weight: 500;
  font-size: 10px;
  line-height: 18px;
  text-align: center;
  color: var(--text);
}

.timer__items {
  display: flex;
  font-weight: 900;
  font-size: 28px;
  line-height: 18px;
  color: var(--light-text);
  letter-spacing: -3.7px;
}

.timer__item {
  display: block;
  min-width: 38px;
  text-align: center;
  color: var(--bright);
  letter-spacing: 0;
}

.timer__end {
  display: block;
  font-weight: 900;
  font-size: 10px;
  line-height: 18px;
  text-align: center;
  color: var(--bright);
}

.button {
  position: relative;
  display: inline-block;
  padding: 20px 40px;
  font-weight: 900;
  font-size: 14px;
  line-height: 18px;
  font-family: inherit;
  text-align: center;
  color: var(--body);
  text-transform: uppercase;
  text-decoration: none;
  background-color: var(--color-button);
  background-image: url(../img/glow.svg);
  background-repeat: no-repeat;
  background-position: -80px center;
  border: 0;
  border-radius: 8px;
  box-shadow: 0 4px 0 var(--bright-light);
  transition: opacity var(--transition-duration) var(--transition-timing-function), box-shadow var(--transition-duration) var(--transition-timing-function), background var(--transition-duration) var(--transition-timing-function);
  appearance: none;
}

.button:hover {
  box-shadow: 0 2px 0 var(--bright-light);
  background-position: right -80px center;
}

.button:active {
  box-shadow: none;
}

.hero {
  overflow: hidden;
}

.hero__wrap::after {
  content: '';
  display: block;
  width: 465px;
  height: 376px;
  margin: 0 auto;
  background-image: url(../img/hero/alina.png);
  background-repeat: no-repeat;
  transform: translateX(-45px);
}

.hero__text {
  max-width: 495px;
  margin: 0 -10px 5px;
  padding: 10px;
  font-weight: 500;
  font-size: 11px;
  line-height: 16px;
  text-transform: uppercase;
  background-color: var(--light-text);
  border-radius: 9px;
}

.hero__title {
  position: relative;
  max-width: 650px;
  margin: 0 0 22px;
  font-weight: 900;
  font-size: 40px;
  line-height: 1;
  color: var(--bright);
  text-transform: uppercase;
  text-shadow: 1px 0 0 var(--bright-light), 0 2px 0 var(--bright-light);
}

.hero__title::before {
  content: '«';
  position: absolute;
  left: -30px;
  color: rgba(0, 0, 0, 0.15);
  text-shadow: none;
}

.hero__title::after {
  content: '»';
  color: rgba(0, 0, 0, 0.15);
  text-shadow: none;
}

.hero__subtitle {
  max-width: 310px;
  margin: 0;
  margin-bottom: 25px;
  padding-right: 20px;
  padding-left: 50px;
  font-weight: 500;
  font-size: 13px;
  line-height: 18px;
  background-image: url(../img/hero/aplle.svg);
  background-repeat: no-repeat;
}

.hero__button {
  position: relative;
  box-sizing: border-box;
  max-width: 320px;
  margin-top: 26px;
  margin-right: -20px;
  margin-left: -50px;
}

.hero__button::after {
  content: '';
  position: absolute;
  top: -70px;
  right: -20px;
  width: 28px;
  height: 58px;
  background-image: url(../img/arrow-from-right-to-bottom.svg);
  background-repeat: no-repeat;
  background-size: contain;
  transform: scale(-1, 1);
}

.gift {
  margin-top: -40px;
  padding-right: 10px;
  padding-left: 10px;
}

.gift__wrap {
  padding: 20px;
  padding-bottom: 0;
  overflow: hidden;
  background-color: #fff;
  background-clip: padding-box;
  border: 10px solid rgba(245, 245, 245, 0.9);
  border-radius: 28px;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
}

.gift__title {
  margin: 0 0 22px;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
