}

.gift__article {
  position: relative;
  max-width: 500px;
  margin: 0 auto;
  padding-bottom: 22px;
  text-align: center;
  background-color: #fbf5f4;
}

.gift__article::after {
  content: url(../img/gift/lime.png);
  position: absolute;
  top: -30px;
  right: -93px;
}

@supports (content: url('../img/gift/lime.webp')) {
  .gift__article::after {
    content: url(../img/gift/lime.webp);
  }
}

.gift__article::before {
  content: url(../img/gift/salmon.png);
  position: absolute;
  bottom: -4px;
  left: -85px;
}

@supports (content: url('../img/gift/salmon.webp')) {
  .gift__article::before {
    content: url(../img/gift/salmon.webp);
  }
}

.gift__article-title {
  margin: 0 0 12px;
  padding: 12px 0;
  font-size: 12px;
  color: #fffbf9;
  background-color: #5b555e;
}

.gift__article p {
  margin: 0;
  margin-bottom: 12px;
  padding-right: 38px;
  padding-left: 38px;
  font-size: 13px;
  line-height: 18px;
}

.gift__button {
  margin-top: 22px;
}

.about__heading {
  margin-top: 12px;
}

.about__text {
  position: relative;
  max-width: 390px;
  margin: 0 auto 10px;
}

.about__text::before {
  content: url(../img/about/alina.png);
  display: block;
  margin: 0 auto -20px;
  transform: translateX(20px);
}

.about__text p {
  box-sizing: border-box;
  max-width: 350px;
  margin: 0;
  margin-right: auto;
  margin-left: auto;
  padding: 14px 34px;
  font-size: 13px;
  line-height: 18px;
  text-align: center;
  border-radius: 20px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
}

.about__text p:not(:last-child) {
