}

.about__img-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
  width: fit-content;
  margin: 0 auto;
}

.about__imgabout__img {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.about__imgabout__img--slide-1 {
  width: 241px;
  height: 175px;
  background-image: url(../img/about/slide-1.png);
}

.about__imgabout__img--slide-2 {
  width: 158px;
  height: 192px;
  background-image: url(../img/about/slide-2.png);
}

.about__imgabout__img--slide-3 {
  width: 232px;
  height: 134px;
  background-image: url(../img/about/slide-3.png);
}

.if__list-big {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 20px;
}

.if__list-small {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 30px;
}

.if__list-small .if__item {
  width: calc(50% - 10px);
  margin-bottom: 20px;
}

.if__list-small .if__item:last-child {
  width: 100%;
  margin-bottom: 0;
}

.if__item {
  box-sizing: border-box;
  padding: 20px 8px 18px;
  font-size: 11px;
  line-height: 16px;
  text-align: center;
  border-radius: 20px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
}

.if__item::before {
  content: '';
  display: block;
  width: 70px;
  height: 70px;
  margin: 0 auto 18px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 50%;
}

.if__item--big {
  margin: 0 0 20px;
  padding: 20px 28px 16px;
  font-size: 13px;
  line-height: 18px;
}

.if__item--big::before {
  width: 100px;
  height: 100px;
  margin: 0 auto 20px;
}

.if__item--1::before {
  background-image: url(../img/if/slide-1.png);
}

.if__item--2::before {
  background-image: url(../img/if/slide-2.png);
}

.if__item--3::before {
  background-image: url(../img/if/slide-3.png);
}

.if__item--4::before {
  background-image: url(../img/if/slide-4.png);
}

.if__item--5::before {
  background-image: url(../img/if/slide-5.png);
}

.if__item--6::before {
  background-image: url(../img/if/slide-6.png);
}

.if__item--7::before {
  background-image: url(../img/if/slide-7.png);
}

.if__item--8::before {
  background-image: url(../img/if/slide-8.png);
}

.if__button {
  display: block;
  width: fit-content;
  margin: 0 auto;
}

.program__wrap {
  padding-bottom: 80px;
}

.program__content {
  position: relative;
  padding: 10px;
  color: var(--text);
  background-color: var(--body);
  border-radius: 30px;
}

.program__content p {
  margin: 0 0 14px;
  font-size: 13px;
  line-height: 18px;
}

.program__content .timer {
  margin: 0 auto 18px;
  padding: 18px 26px;
  border-radius: 30px;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
}

.program__content .timer__title {
  margin-bottom: 10px;
  font-size: 13px;
  line-height: 22px;
}

.program__content .timer__items {
  margin-bottom: 14px;
  font-size: 40px;
  letter-spacing: -3px;
}

.program__content .timer__item {
  min-width: 52px;
  letter-spacing: -2px;
}

.program__content .timer__end {
  font-size: 13px;
  line-height: 22px;
}

.program__list {
  margin: 0 0 20px;
  padding: 0;
  list-style-type: none;
}

.program__item {
  padding-left: 24px;
  font-size: 13px;
  line-height: 18px;
  background-image: url(../img/checkmark.svg);
  background-repeat: no-repeat;
  background-size: 16px auto;
}

.program__item:not(:last-child) {
  margin-bottom: 12px;
}

.program__button {
  margin-bottom: 12px;
}

.result .swiper-container {
  width: 265px;
}

.result__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.result__item {
  width: 265px;
  padding: 10px;
  box-sizing: border-box;
  background-color: var(--bright-dark);
  border-radius: 30px;
  text-align: center;
}

.result__item img {
  display: block;
  max-width: none;
  margin: -10px -10px 0 -10px;
  border-radius: 30px;
}

.review {
  position: relative;
  z-index: 1;
  padding-right: 20px;
  padding-left: 20px;
}

.review::before {
  content: '';
  position: absolute;
  top: -10px;
  left: 50%;
  width: calc(100% - 20px);
  max-width: 1200px;
  height: 100%;
  background-color: rgba(250, 250, 250, 0.9);
  border-radius: 38px 38px 15% 15%;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.01);
  transform: translateX(-50%);
}

.review::after {
  content: '';
  position: absolute;
  top: -10px;
  left: 50%;
  width: 100vw;
  height: calc(100% + 20px);
  background-image: linear-gradient(to bottom, transparent, #fff, #fff);
  box-shadow: 0 50px 50px #fff;
  transform: translateX(-50%);
}

.review .swiper-container {
  width: 265px;
}

.review__wrap {
  z-index: 1;
  padding-right: 0;
  padding-left: 0;
  background-color: #fff;
  border-radius: 28px;
}

.review__heading {
  margin-top: 2px;
  margin-bottom: 12px;
  font-size: 18px;
  line-height: 24px;
  color: var(--bright);
}

.review__list {
  margin: 0 0 30px;
  padding: 0;
  list-style-type: none;
}

.review__sub-heading {
  margin: 0 0 30px;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
}

.review__item img {
  border-radius: 20px 0 20px;
}

.review__item p {
  margin-bottom: 0;
  font-size: 13px;
  line-height: 20px;
}

.review__user {
  position: relative;
}

.review__name-user {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 2px 8px;
  color: var(--body);
  background-color: var(--bright);
}

.review__button {
  display: block;
  width: fit-content;
  margin-right: auto;
  margin-left: auto;
}

.form {
  position: relative;
  z-index: 1;
}

.form__text {
  max-width: 495px;
