}

.form__content form {
  padding: 0 10px;
}

.form__label {
  position: relative;
  display: block;
}

.form__label span {
  position: absolute;
  top: 50%;
  left: 6px;
  z-index: -1;
  font: 10px;
  font-weight: 700;
  font-size: 10px;
  line-height: 20px;
  color: transparent;
  text-transform: uppercase;
  letter-spacing: 1px;
  background-color: transparent;
  transition: color var(--transition-duration) var(--transition-timing-function), background-color var(--transition-duration) var(--transition-timing-function);
}

.form__control {
  box-sizing: border-box;
  width: 100%;
  height: 52px;
  margin-bottom: 18px;
  padding: 18px 20px;
  font-size: 16px;
  line-height: 1;
  font-family: inherit;
  color: inherit;
  background-color: #fff;
  border: 2px solid #e5e5e5;
  border-radius: 6px;
  outline: 0;
  transition: border var(--transition-duration) var(--transition-timing-function);
