.about {
	@media (min-width: @min-lg) {
		padding-top: 118px;
	}
}

.about__heading {
	margin-top: 12px;
}

.about__text {
	position: relative;
	max-width: 390px;
	margin: 0 auto 10px;

	@media (min-width: @min-md) {
		margin-left: 0;
	}

	@media (min-width: @min-lg) {
		padding-top: 130px;
		padding-bottom: 68px;
	}

	@media (min-width: @min-xl) {
		padding-left: 220px;
	}

	&::before {
		content: url(../../../img/about/alina.png);

		display: block;
		margin: 0 auto -20px;
		transform: translateX(20px);

		@media (min-width: @min-md) {
			position: absolute;
			top: 0;
			right: 0;
			transform: translateX(100%);
		}

		@media (min-width: @min-lg) {
			content: url(../../../img/about/alina@desktop.png);
		}
	}

	& p {
		box-sizing: border-box;
		max-width: 350px;
		margin: 0;
		margin-right: auto;
		margin-left: auto;
		padding: 14px 34px;
		font-size: 13px;
		line-height: 18px;
		text-align: center;
		border-radius: 20px;
		box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);

		@media (min-width: @min-lg) {
			padding: 30px 50px;
			font-size: 16px;
			line-height: 24px;
		}

		@media (min-width: @min-md) {
			&:nth-child(odd) {
				margin-right: 0;
			}
		}

		&:not(:last-child) {
			margin-bottom: 20px;
		}

	}
}

.about__img-list {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-end;
	width: fit-content;
	margin: 0 auto;
}

.about__imgabout__img {
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;

	&--slide-1 {
		width: 241px;
		height: 175px;
		background-image: url(../../../img/about/slide-1.png);

		@media (min-width: @min-lg) {
			width: 402px;
			height: 292px;
			background-image: url(../../../img/about/slide-1@desktop.png);
		}
	}

	&--slide-2 {
		width: 158px;
		height: 192px;
		background-image: url(../../../img/about/slide-2.png);

		@media (min-width: @min-lg) {
			width: 264px;
			height: 320px;
			background-image: url(../../../img/about/slide-2@desktop.png);
		}
	}

	&--slide-3 {
		width: 232px;
		height: 134px;
		background-image: url(../../../img/about/slide-3.png);

		@media (min-width: @min-lg) {
			width: 387px;
			height: 224px;
			background-image: url(../../../img/about/slide-3@desktop.png);
		}
	}
}
