.hero {
	overflow: hidden;

	@media (min-width: @min-lg) {
		padding-bottom: 36px;
	}

	@media (min-width: @min-lg) {
		overflow: visible;
	}
}

.hero__wrap {
	@media (min-width: @min-lg) {
		padding-top: 40px;
	}

	&::after {
		content: '';
		display: block;
		width: 465px;
		height: 376px;
		margin: 0 auto;
		background-image: url(../../../img/hero/alina.png);
		background-repeat: no-repeat;
		transform: translateX(-45px);

		@media (min-width: @min-lg) {
			position: absolute;
			top: -120px;
			left: calc(50% - 40px);
			z-index: -1;
			width: 1000px;
			height: 900px;
			background-image: url(../../../img/hero/alina@desktop.png);
		}
	}
}

.hero__text {
	max-width: 495px;
	margin: 0 -10px 5px;
	padding: 10px;
	font-weight: 500;
	font-size: 11px;
	line-height: 16px;
	text-transform: uppercase;
	background-color: var(--light-text);
	border-radius: 9px;

	@media (min-width: @min-sm) {
		font-size: 14px;
		line-height: 20px;
	}

	@media (min-width: @min-lg) {
		max-width: 334px;
		margin-bottom: 28px;
	}
}

.hero__title {
	position: relative;
	max-width: 650px;
	margin: 0 0 22px;
	font-weight: 900;
	font-size: 40px;
	line-height: 1;
	color: var(--bright);
	text-transform: uppercase;
	text-shadow: 1px 0 0 var(--bright-light),
		0 2px 0 var(--bright-light);

	@media (min-width: @min-md) {
		font-size: 100px;
		line-height: 90px;
	}

	&::before {
		content: '«';
		position: absolute;
		left: -30px;
		color: rgba(0, 0, 0, 0.15);
		text-shadow: none;

		@media (min-width: @min-md) {
			left: -40px;
			font-size: 64px;
		}
	}

	&::after {
		content: '»';
		color: rgba(0, 0, 0, 0.15);
		text-shadow: none;

		@media (min-width: @min-md) {
			font-size: 64px;
		}
	}
}

.hero__subtitle {
	max-width: 310px;
	margin: 0;
	margin-bottom: 25px;
	padding-right: 20px;
	padding-left: 50px;
	font-weight: 500;
	font-size: 13px;
	line-height: 18px;
	background-image: url(../../../img/hero/aplle.svg);
	background-repeat: no-repeat;

	@media (min-width: @min-md) {
		box-sizing: border-box;
		max-width: 550px;
		font-size: 16px;
		line-height: 24px;
	}
}

.hero__button {
	position: relative;
	box-sizing: border-box;
	max-width: 320px;
	margin-top: 26px;
	margin-right: -20px;
	margin-left: -50px;

	@media (min-width: @min-md) {
		margin-top: 35px;
	}

	@media (min-width: @min-md) {
		margin-top: 35px;
	}

	&::after {
		content: '';
		position: absolute;
		top: -70px;
		right: -20px;
		width: 28px;
		height: 58px;
		background-image: url(../../../img/arrow-from-right-to-bottom.svg);
		background-repeat: no-repeat;
		background-size: contain;
		transform: scale(-1, 1);

		@media (min-width: 395px) {
			top: -25px;
			right: -85px;
			width: 63px;
			height: 80px;
			transform: scale(-1, 1) rotate(-50deg);
		}
	}
}
