.program__wrap {
	padding-bottom: 80px;
}

.program__content {
	position: relative;
	padding: 10px;
	color: var(--text);
	background-color: var(--body);
	border-radius: 30px;



	@media (min-width: @min-sm) {
		padding-right: 20px;
		padding-left: 20px;
	}

	@media (max-width: @max-md) {
		&::after {
			content: url(../../../img/program/alina.png);
			display: block;
			width: fit-content;
			margin: 0 auto -105px;
			transform: translateX(-40px);
		}
	}

	@media (min-width: @min-md) {
		padding-left: 356px;

		&::before {
			content: url(../../../img/program/alina@desktop.png);
			position: absolute;
			top: -78px;
			left: -70px;
		}
	}

	@media (min-width: @min-lg) {
		margin-top: 90px;
		padding-bottom: 54px;
	}

	& p {
		margin: 0 0 14px;
		font-size: 13px;
		line-height: 18px;

		@media (min-width: @min-lg) {
			font-size: 16px;
			line-height: 22px;
		}
	}
}

.program__content {
	& .timer {
		margin: 0 auto 18px;
		padding: 18px 26px;
		border-radius: 30px;
		box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);

		@media (min-width: @min-lg) {
			margin-top: -90px;
			margin-left: 0;
			padding: 30px 40px;
			border-radius: 18px;
		}
	}

	& .timer__title {
		margin-bottom: 10px;
		font-size: 13px;
		line-height: 22px;

		@media (min-width: @min-lg) {
			margin-bottom: 14px;
			font-size: 16px;
			line-height: 22px;
		}
	}

	& .timer__items {
		margin-bottom: 14px;
		font-size: 40px;
		letter-spacing: -3px;

		@media (min-width: @min-lg) {
			margin-bottom: 18px;
			font-size: 60px;
			letter-spacing: -5px;
		}
	}

	& .timer__item {
		min-width: 52px;
		letter-spacing: -2px;

		@media (min-width: @min-lg) {
			min-width: 78px;
		}
	}

	& .timer__end {
		font-size: 13px;
		line-height: 22px;

		@media (min-width: @min-lg) {
			font-size: 16px;
			line-height: 22px;
		}
	}
}

.program__list {
	margin: 0 0 20px;
	padding: 0;
	list-style-type: none;

	@media (min-width: @min-lg) {
		margin-bottom: 24px;
	}
}

.program__item {
	padding-left: 24px;
	font-size: 13px;
	line-height: 18px;
	background-image: url(../../../img/checkmark.svg);
	background-repeat: no-repeat;
	background-size: 16px auto;

	@media (min-width: @min-lg) {
		min-height: 20px;
		margin-bottom: 14px;
		background-size: 20px auto;

		@media (min-width: @min-lg) {
			font-size: 16px;
			line-height: 22px;
		}
	}

	&:not(:last-child) {
		margin-bottom: 12px;
	}
}

.program__button {
	margin-bottom: 12px;

	@media (min-width: @min-lg) {
		margin-top: 30px;
		margin-bottom: 0;
	}
}
