.header {
	display: block;
	box-shadow: 0 50px 50px rgba(0, 0, 0, 0.1);

	@media (min-width: @min-lg) {
		.timer {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}
}

.header__wrap {
	display: flex;
	justify-content: space-between;
	align-items: center;
	min-height: calc(70px - 16px);
	padding-top: 8px;
	padding-bottom: 8px;

	@media (min-width: @min-md) {
		min-height: calc(120px - 44px);
		padding-top: 22px;
		padding-bottom: 22px;
	}
}
