html {
	scroll-behavior: smooth;
}

body {
	margin: 0;
	font-weight: 500;
	font-family: "Montserrat", sans-serif;
	color: var(--text);
	font-smooth: antialiased;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-size-adjust: 100%;
}

img {
	max-width: 100%;
}

.visually-hidden {
	position: absolute;
	width: 0;
	height: 0;
	margin: 0;
	padding: 0;
	overflow: hidden;
	border: none;
}

[class$="__wrap"] {
	position: relative;
	box-sizing: border-box;
	max-width: 1200px;
	margin-right: auto;
	margin-left: auto;
	padding-top: 20px;
	padding-right: 20px;
	padding-bottom: 20px;
	padding-left: 20px;

	@media (min-width: @min-md) {
		padding-right: 40px;
		padding-left: 40px;
	}
}

main {
	position: relative;
	z-index: 1;
}

.overflow-hidden {
	overflow: hidden;
}

.bright {
	color: var(--bright);
}

[class$="__heading"] {
	position: relative;
	z-index: 1;
	margin: 0 0 20px;
	font-weight: 500;
	font-size: 22px;
	line-height: 30px;
	text-align: center;

	@media (min-width: @min-lg) {
		font-size: 40px;
		line-height: 50px;
	}
}

.wrap-bright-section {
	margin-bottom: -80px;
	padding-top: 50px;
	padding-bottom: 104px;
	background-image: url(../../img/bg-green.svg);
	background-position: center;
	background-size: 1920px 100%;

	@media (min-width: @min-lg) {
		margin-bottom: -260px;
		padding-bottom: 360px;
	}

	& section {
		color: var(--body);
		background-color: var(--bright);
	}
}

.mark {
	color: var(--body);
	background-color: var(--mark);
}

.mark-color {
	color: var(--mark);
}

::placeholder {
	color: #a8a8a8;
}
