.logo__link {
	display: block;

	@media (min-width: @min-sm) {
		& img {
			width: 186px;
			height: 61px;
		}
	}

	& img {
		display: block;
	}
}

.logo {
	@media (min-width: @min-md) {
		&::after {
			content: url(../../../img/header/alina.png);
			position: absolute;
			bottom: -4px;
			left: 205px;
			display: block;
		}
	}
}
